        return this[params]
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      Object.keys(this.itemTransQueryPath).forEach(item => {
        this.queryHandler(1, item)
      })
      this.initMonthSelect()
    })
  },
  methods: {
    // 
    // performanceOfWeek () {
    //   const lastFriday = moment().week(moment().week() - 1).weekday(5).format('YYYY-MM-DD')
