<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item
        v-if="showCity"
        label="">
        <select-all
          :data-list="cityList"
          v-model="city"
          :init-select="city"
          value-key="id"
          label-key="deptName"
          @change="changCity"/>
        <span
          v-if="cityList.length > 1"
          class="remarks"></span>
      </el-form-item>
      <el-form-item
        v-if="showRegion"
        label="" >
        <select-all
          :data-list="regionList"
          v-model="region"
          :init-select="region"
          value-key="id"
          label-key="deptName"
          @change="changeRegion"/>
        <span
          v-if="regionList.length > 1 || cityList.length > 1"
          class="remarks"></span>
      </el-form-item>
      <el-form-item
        v-if="showService"
        label="">
        <select-all
          :data-list="serviceList"
          v-model="service"
          :init-select="service"
          value-key="id"
          label-key="deptName"
          @change="changeService"/>
        <span
          v-if="serviceList.length > 1 || regionList.length > 1"
          class="remarks"></span>
      </el-form-item>
      <el-form-item
        v-if="showService"
        label="" >
        <el-radio
          size="small"
          v-if="showCity"
          v-model="collect"
          label="1"></el-radio>
        <el-radio
          size="small"
          v-if="showRegion"
          v-model="collect"
          label="2"></el-radio>
        <el-radio
          v-model="collect"
          label="3"></el-radio>
        <el-radio
          size="small"
          v-if="showRegion"
          v-model="collect"
          label="4"></el-radio>
        <el-radio
          size="small"
          v-if="roleId === 5 || roleId === 4"
          v-model="collect"
          label="5"></el-radio>
      </el-form-item>
      <el-form-item
        v-show="(collect === '4' || collect === '5') && showService"
        label="">
        <select-all
          :data-list="usersList"
          v-model="users"
          :init-select="users"
          value-key="userId"
          label-key="userName"/>
        <span class="remarks"></span>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
//  1: 2:; 3: ; 10:;11: ; 12: ; 13:   14:
//  1  2  3  4  5  6  7 

//  
import selectAll from './selectAll'
// import cloneDeep from 'lodash/cloneDeep'
import util from '@/common/util'
import {
  getDepartChildrenHistory as getDepartList,
  getUserByIdentityHistory as getUsers,
  getIdentityHistory,
  getDepartHistory
} from '../../api/crmPanel'
// const urls = {
//   getAllDentityUser: '/xinxin/i/identity/history',
//   getAllDentity: '/xinxin/i/identity/history/depart',
//   getDepartList: '/xinxin/i/depart/children/history',
//   getUsers: '/xinxin/i/identity/history/user'
// }
export default {
  components: {
    selectAll
  },
  props: {
    initParams: {
      type: Object,
      default: function () {
        return {
          cities: '',
          areas: '',
          servcenters: '',
          users: ''
        }
      }
    },
    startTime: {
      type: String,
      default: ''
    },
    endTime: {
      type: String,
      default: ''
    },
    hasTime: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      cityList: [],
      regionList: [],
      serviceList: [],
      city: [],
      region: [],
      service: [],
      collect: '1', //  '12345()'
      usersList: [],
      users: [],
      params: {},
      initGetList: true // 
    }
  },
  // collect   collect 45
  computed: {
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    },
    roleId () {
      return this.userInfo.crmRoleId
    },
    showCity () {
      return this.roleId === 1 || this.roleId === 2
    },
    showRegion () {
      return this.roleId === 1 || this.roleId === 2 || this.roleId === 3
    },
    showService () {
      return (
        this.roleId === 1 ||
        this.roleId === 2 ||
        this.roleId === 3 ||
        this.roleId === 4
      )
    },
    handler () {
      const { roleId, endTime, hasTime, startTime } = this
      return { roleId, endTime, hasTime, startTime }
    },
    handlerParams () {
      const { city, region, service, users } = this
      return { city, region, service, users }
    }
  },
  watch: {
    //  
    handlerParams (val) {
      this.emithandle()
    },
    collect (val) {
      this.emithandle()
      if (this.roleId !== 5) {
        this.getUsersList(this.service, 1)
      }
    },
    // hasTime    
    handler (val) {
      const { roleId, endTime, hasTime } = val
      if (!roleId) return
      if (hasTime && endTime) {
        this.init(this.userInfo)
      } else if (!hasTime) {
        this.init(this.userInfo)
      }
    }
  },
  mounted () {
    this.params = { ...this.initParams }
    if (this.roleId && !this.hasTime) {
      // this.init(this.userInfo)
      this.init()
    }
  },
  methods: {
    async init () {
      switch (this.roleId) {
        // 
        case 1:
          this.getNewCollect('1')
          this.getAllCity()
          break
        // 
        case 2:
          this.getNewCollect('1')
          this.getMyCity()
          break
        // 
        case 3:
          this.getNewCollect('2')
          this.getRegionList([])
          break
        // 
        case 4:
          this.getServiceList([])
          this.getNewCollect('3')
          break
        // 
        case 5:
          this.collect = '5'
          this.getUsersList([])
          break
      }
    },
    // 
    emithandle () {
      let params = {
        collect: this.collect,
        cities: this.city.join(','),
        areas: this.region.join(','),
        servcenters: this.service.join(','),
        users: this.users.join(',')
      }
      // users
      if (!(params.collect === '4' || params.collect === '5')) {
        params.users = ''
      }
      this.$emit('getParams', params)
      if (!this.initGetList) {
        return
      }
      // 
      switch (this.roleId) {
        case 5:
          //  
          if (this.users.length > 0) {
            this.initGetList = false
            // 
            this.$emit('getInitList')
          }
          break
        default:
          //  
          if (this.collect === '4' || this.collect === '5') {
            if (this.users.length > 0) {
              this.initGetList = false
              this.$emit('getInitList')
            }
          } else {
            //  
            if (this.service.length > 0) {
              this.initGetList = false
              this.$emit('getInitList')
            }
          }
          break
      }
    },
    changCity (val) {
      this.getRegionList(val)
    },
    changeRegion (val) {
      this.getServiceList(val)
    },
    changeService (val) {
      this.getUsersList(val)
    },
    //   
    async getAllDentity () {
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0
      }
      // let { data } = await this.$axios.post(urls.getAllDentity, params)
      try {
        const data = await getDepartHistory(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '' + err.message
        })
        return []
      }
    },
    // deptPid
    async getDepart (val) {
      if (val && val.length < 1) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0,
        deptPids: val
      }
      // let { data } = await this.$axios.post(urls.getDepartList, params)
      // const data = await getDepartList(params)
      // return data || []
      try {
        const data = await getDepartList(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: 'deptPid' + err.message
        })
        return []
      }
    },
    // 
    async getUsers (val) {
      let searchIds = [...val]
      if (!(this.collect === '4' || this.collect === '5') || val.length <= 0) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0,
        deptId: searchIds.join(',')
      }
      // let { data } = await this.$axios.post(urls.getUsers, params)
      // const data = await getUsers(params)
      // return data || []
      try {
        const data = await getUsers(params)
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '' + err.message
        })
        return []
      }
    },
    //  
    async getUsersInit () {
      if (!(this.collect === '4' || this.collect === '5')) {
        return []
      }
      let params = {
        startTime: util.getTimestamp(this.startTime, 10) || 0,
        endTime: util.getTimestamp(this.endTime, 10) || 0
      }
      // let { data } = await this.$axios.post(urls.getAllDentityUser, params)
      // let data = await getIdentityHistory(params)
      //  
      // if (data && data.length <= 0) {
      //   data = [
      //     { userId: this.userInfo.userId, userName: this.userInfo.userName }
      //   ]
      // }
      // return data || []

      try {
        let data = await getIdentityHistory(params)
        if (data && data.length <= 0) {
          data = [
            { userId: this.userInfo.userId, userName: this.userInfo.userName }
          ]
        }
        return data || []
      } catch (err) {
        this.$Message.error({
          content: '' + err.message
        })
        return []
      }
    },
    // 
    async getAllCity () {
      //  11
      this.cityList = await this.getDepart(['10'])
      let ids = this.cityList.map(item => item.id)
      // 
      if (this.params.cities.length > 0) {
        //  stringint
        this.city = this.strToIntArray(this.initParams.cities)
        this.params.cities = ''
      } else {
        // 
        this.city = ids
      }
      this.getRegionList(this.city)
    },

    // 
    async getMyCity () {
      this.cityList = await this.getAllDentity()
      let ids = this.cityList.map(item => item.id)
      // 
      if (this.params.cities.length > 0) {
        //  stringint
        this.city = this.strToIntArray(this.initParams.cities)
        this.params.cities = ''
      } else {
        // 
        this.city = ids
      }
      this.getRegionList(this.city)
    },
    // 
    async getRegionList (val) {
      //  
      let searchIds = [...val]
      if (val.includes(12)) {
        let index = searchIds.indexOf(12)
        searchIds.splice(index, 1)
      }
      //  
      this.regionList =
        this.roleId === 3
          ? await this.getAllDentity()
          : await this.getDepart(searchIds)
      let ids = this.regionList.map(item => item.id)
      // 
      if (this.params.areas.length > 0) {
        this.region = this.strToIntArray(this.initParams.areas)
        this.params.areas = ''
      } else {
        // 
        this.region = ids
      }
      this.getServiceList(this.region)
    },
    // 
    async getServiceList (val) {
      //  
      let searchIds = [...val]
      if (this.city.includes(12)) {
        searchIds = [12].concat(searchIds)
      }
      //  
      this.serviceList =
        this.roleId === 4
          ? await this.getAllDentity()
          : await this.getDepart(searchIds)
      let ids = this.serviceList.map(item => item.id)

      this.service = ids
      // 
      if (this.params.servcenters.length > 0) {
        this.service = this.strToIntArray(this.initParams.servcenters)
        this.params.servcenters = ''
      } else {
        // 
        this.service = ids
      }
      this.getUsersList(this.service)
    },
    // 
    async getUsersList (val, type) {
      // type =1  
      let usersList =
        this.roleId === 5 ? await this.getUsersInit() : await this.getUsers(val)
      let idObj = {}
      let newList = []
      usersList.forEach(item => {
        if (!idObj[item.userId]) {
          idObj[item.userId] = true
          newList.push(item)
        }
      })
      this.usersList = [...newList]
      let ids = this.usersList.map(item => item.userId)
      // 
      if (this.params.users.length > 0 && (type !== 1 || this.roleId === 4)) {
        this.users = this.strToIntArray(this.params.users)
        this.params.users = ''
      } else {
        // 
        this.users = ids
      }
    },
    // 
    getNewCollect (val) {
      if (this.params.collect) {
        this.collect = this.initParams.collect
        this.params.collect = ''
      } else {
        this.collect = val
      }
    },
    //  stringint
    strToIntArray (val) {
      let list = val.split(',')
      list.forEach((item, index) => {
        list[index] = parseInt(item)
      })
      return list
    }
  }
}
</script>

