<template>
  <div class="from-item-400">
    <el-form
      label-width="120px"
      size="medium">
      <el-form-item :label="label">
        <el-select
          size="small"
          v-model="type"
          placeholder="">
          <el-option
            v-for="item in list"
            :key="item"
            :label="item"
            :value="item"/>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    label: {
      type: String,
      default: ''
    },
    paramKey: {
      type: String,
      default: 'subjectid'
    }
  },
  data () {
    return {
      list: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      type: ''
    }
  },
  watch: {
    type (val) {
      let type = val
      if (val === '') {
        type = 'all'
      }
      let params = {}
      params[this.paramKey] = type
      this.$emit('getParams', params)
    }
  },
  mounted () {
    this.type = ''
  },
  methods: {}
}
</script>

