const verify = (params, type) => {
  // type === 2 
  let val = { msg: '', check: true }
  const { collect, cities, areas, servcenters, users } = params
  switch (collect) {
    case '1': // 
      if (!cities) {
        val.check = false
        val.msg = ''
      }
      break
    case '2': // 
      if (!areas) {
        val.check = false
        val.msg = ''
      }
      break
    case '3': // 
      if (!servcenters) {
        val.check = false
        val.msg = ''
      }
      break
    default:
      // 
      let msg = ''
      if (type === 2) {
        msg = ''
      }
      if (!users) {
        val.check = false
        val.msg = msg
      }
      break
  }
  return val
}
export default verify
